import React, { useState, useEffect, useRef } from "react";
import { useGoogleLogin, googleLogout } from '@react-oauth/google';
import { ExpenseList, ExpenseForm, LoadingBar } from "./components/index";
import { MDCSnackbar } from "@material/snackbar/dist/mdc.snackbar.js";

import "@material/fab/dist/mdc.fab.css";
import "@material/button/dist/mdc.button.css";
import "@material/toolbar/dist/mdc.toolbar.css";
import "@material/snackbar/dist/mdc.snackbar.css";
import "@material/card/dist/mdc.card.css";

import "./App.css";

// Helper to format dates
const formatDate = (dateObj) => {
  const year = dateObj.getFullYear();
  const month = dateObj.getMonth() < 9 ? "0" + (dateObj.getMonth() + 1) : dateObj.getMonth() + 1;
  const day = dateObj.getDate() < 10 ? "0" + dateObj.getDate() : dateObj.getDate();
  return `${year}-${month}-${day}`;
};

const SPREADSHEET_ID = process.env.REACT_APP_SHEET_ID;

const App = () => {
  // --- STATE MANAGEMENT ---
  const [signedIn, setSignedIn] = useState(false);
  const [processing, setProcessing] = useState(false); // Default false, waits for login
  const [showExpenseForm, setShowExpenseForm] = useState(false);
  
  // Data State
  const [accounts, setAccounts] = useState([]);
  const [categories, setCategories] = useState([]);
  const [expenses, setExpenses] = useState([]);
  const [currentMonth, setCurrentMonth] = useState(undefined);
  const [previousMonth, setPreviousMonth] = useState(undefined);
  
  // Form State
  const [expense, setExpense] = useState({});

  // Refs
  const snackbarRef = useRef(null);
  const snackbarInstance = useRef(null);

  // --- GOOGLE AUTHENTICATION HOOK ---
  const login = useGoogleLogin({
    onSuccess: async (tokenResponse) => {
      console.log("Login Success, Token:", tokenResponse);
      
      // Load the GAPI client for Sheets API calls
      await loadGapiClient();
      
      // Set the access token for GAPI to use
      window.gapi.client.setToken({
        access_token: tokenResponse.access_token,
      });

      setSignedIn(true);
      loadData();
    },
    onError: error => console.log('Login Failed:', error),
    scope: 'https://www.googleapis.com/auth/spreadsheets https://www.googleapis.com/auth/drive.metadata.readonly',
  });

  const logout = () => {
    googleLogout();
    setSignedIn(false);
    setExpenses([]);
  };

  // --- GAPI LOADER ---
  const loadGapiClient = () => {
    return new Promise((resolve, reject) => {
      const script = document.createElement("script");
      script.src = "https://apis.google.com/js/api.js";
      script.onload = () => {
        window.gapi.load("client", () => {
          window.gapi.client
            .init({
              discoveryDocs: ["https://sheets.googleapis.com/$discovery/rest?version=v4"],
            })
            .then(() => {
              resolve();
            });
        });
      };
      document.body.appendChild(script);
    });
  };

  // --- INITIALIZATION ---
  useEffect(() => {
    // Initialize Snackbar
    if (snackbarRef.current) {
      snackbarInstance.current = new MDCSnackbar(snackbarRef.current);
    }

    // Keyboard Listeners
    const handleKeyUp = (e) => {
      if (signedIn) {
        if (!showExpenseForm && e.keyCode === 65) { // 'a' key
          onExpenseNew();
        } else if (showExpenseForm && e.keyCode === 27) { // escape
          handleExpenseCancel();
        }
      }
    };
    document.addEventListener("keyup", handleKeyUp);
    return () => document.removeEventListener("keyup", handleKeyUp);
  }, [signedIn, showExpenseForm]);

  // --- DATA LOGIC ---
  const parseExpense = (value, index) => {
    return {
      id: `Expenses!A${index + 2}`, // Matches original logic
      date: value[0],
      description: value[1],
      category: value[3],
      amount: value[4] ? value[4].replace(",", "") : "0",
      account: value[2]
    };
  };

  const formatExpense = (exp) => {
    return [
      `=DATE(${exp.date.substr(0, 4)}, ${exp.date.substr(5, 2)}, ${exp.date.substr(-2)})`,
      exp.description,
      exp.account,
      exp.category,
      exp.amount
    ];
  };

  const loadData = () => {
    setProcessing(true);
    window.gapi.client.sheets.spreadsheets.values
      .batchGet({
        spreadsheetId: SPREADSHEET_ID,
        ranges: [
          "Data!A2:A50", // Accounts
          "Data!E2:E50", // Categories
          "Expenses!A2:F", // Expenses
          "Current!H1", // Current Month Total
          "Previous!H1" // Prev Month Total
        ]
      })
      .then(response => {
        const result = response.result.valueRanges;
        setAccounts(result[0].values ? result[0].values.map(i => i[0]) : []);
        setCategories(result[1].values ? result[1].values.map(i => i[0]) : []);
        setExpenses((result[2].values || []).map(parseExpense).reverse().slice(0, 30));
        setCurrentMonth(result[3].values ? result[3].values[0][0] : 0);
        setPreviousMonth(result[4].values ? result[4].values[0][0] : 0);
        setProcessing(false);
      })
      .catch(err => {
        console.error("Error loading data", err);
        setProcessing(false);
      });
  };

  // --- ACTIONS ---
  const onExpenseNew = () => {
    const now = new Date();
    setExpense({
      amount: "",
      description: "",
      date: formatDate(now),
      category: categories[0] || "",
      account: accounts[0] || ""
    });
    setShowExpenseForm(true);
  };

  const handleExpenseSubmit = () => {
    setProcessing(true);
    setShowExpenseForm(false);

    const isUpdate = !!expense.id;
    
    // Define the API Call
    const apiCall = isUpdate
      ? window.gapi.client.sheets.spreadsheets.values.update({
          spreadsheetId: SPREADSHEET_ID,
          range: expense.id,
          valueInputOption: "USER_ENTERED",
          values: [formatExpense(expense)]
        })
      : window.gapi.client.sheets.spreadsheets.values.append({
          spreadsheetId: SPREADSHEET_ID,
          range: "Expenses!A1",
          valueInputOption: "USER_ENTERED",
          insertDataOption: "INSERT_ROWS",
          values: [formatExpense(expense)]
        });

    apiCall.then(() => {
      if(snackbarInstance.current) {
        snackbarInstance.current.show({
            message: `Expense ${isUpdate ? "updated" : "added"}!`
        });
      }
      loadData();
    }).catch(err => {
      console.error("Submit Error", err);
      setProcessing(false);
    });
  };

  const handleExpenseDelete = (expToDelete) => {
    setProcessing(true);
    setShowExpenseForm(false);
    
    // Extract Row ID logic from original code
    const expenseRow = expToDelete.id.substring(10); // Removes "Expenses!A"
    
    window.gapi.client.sheets.spreadsheets.batchUpdate({
      spreadsheetId: SPREADSHEET_ID,
      resource: {
        requests: [{
          deleteDimension: {
            range: {
              sheetId: 0, // Assumption: First sheet is Expenses. If not, needs strict ID.
              dimension: "ROWS",
              startIndex: expenseRow - 1,
              endIndex: expenseRow
            }
          }
        }]
      }
    }).then(() => {
      if(snackbarInstance.current) snackbarInstance.current.show({ message: "Expense deleted!" });
      loadData();
    });
  };

  const handleExpenseSelect = (selectedExpense) => {
    setExpense(selectedExpense);
    setShowExpenseForm(true);
  };

  const handleExpenseCancel = () => {
    setShowExpenseForm(false);
  };

  const handleExpenseChange = (attribute, value) => {
    setExpense(prev => ({ ...prev, [attribute]: value }));
  };

  // --- RENDER ---
  const renderBody = () => {
    if (processing) return <LoadingBar />;
    
    if (showExpenseForm) {
      return (
        <ExpenseForm
          categories={categories}
          accounts={accounts}
          expense={expense}
          onSubmit={handleExpenseSubmit}
          onCancel={handleExpenseCancel}
          onDelete={handleExpenseDelete}
          onChange={handleExpenseChange}
        />
      );
    }

    return (
      <div>
        <div className="mdc-card">
          <section className="mdc-card__primary">
            <h2 className="mdc-card__subtitle">This month you've spent:</h2>
            <h1 className="mdc-card__title mdc-card__title--large center">
              {currentMonth || "..."}
            </h1>
          </section>
          <section className="mdc-card__supporting-text">
            Previous month: {previousMonth || "..."}
          </section>
        </div>
        <ExpenseList expenses={expenses} onSelect={handleExpenseSelect} />
        <button
          onClick={onExpenseNew}
          className="mdc-fab app-fab--absolute material-icons"
          aria-label="Add expense"
        >
          <span className="mdc-fab__icon">add</span>
        </button>
      </div>
    );
  };

  return (
    <div>
      <header className="mdc-toolbar mdc-toolbar--fixed">
        <div className="mdc-toolbar__row">
          <section className="mdc-toolbar__section mdc-toolbar__section--align-start">
            <span className="mdc-toolbar__title">Expenses</span>
          </section>
          <section className="mdc-toolbar__section mdc-toolbar__section--align-end" role="toolbar">
            {!signedIn && (
              <a className="material-icons mdc-toolbar__icon" aria-label="Sign in" onClick={() => login()}>
                perm_identity
              </a>
            )}
            {signedIn && (
              <a className="material-icons mdc-toolbar__icon" aria-label="Sign out" onClick={logout}>
                exit_to_app
              </a>
            )}
          </section>
        </div>
      </header>

      <div className="toolbar-adjusted-content">
        {!signedIn ? (
          <div className="center">
            <button className="mdc-button sign-in" aria-label="Sign in" onClick={() => login()}>
              Sign In with Google
            </button>
          </div>
        ) : (
          renderBody()
        )}
      </div>

      <div
        ref={snackbarRef}
        className="mdc-snackbar"
        aria-live="assertive"
        aria-atomic="true"
        aria-hidden="true"
      >
        <div className="mdc-snackbar__text" />
        <div className="mdc-snackbar__action-wrapper">
          <button type="button" className="mdc-button mdc-snackbar__action-button" aria-hidden="true" />
        </div>
      </div>
    </div>
  );
};

export default App;